<template>
  <div class="home2">
    <b-row>
      <b-col class="text-center">
        <div>{{note}}</div>
      </b-col>
    </b-row>
    <hr>
    <b-row>
      <b-col cols="6" class="text-right">{{str_example1}}</b-col>
      <b-col cols="6" class="text-left">{{str_upload1}}</b-col>
    </b-row>
    <b-row>
      <b-col cols="6" class="text-right">
        <label class="imgupload">
          <img src="../assets/1.jpg" alt="Face..." class="img-thumbnail imgupload">
        </label>
      </b-col>
      <b-col cols="6" class="text-left">
        <label class="imgupload">
          <img :src="img1" alt="Face..." class="img-thumbnail imgupload cursor">
          <input
            :disabled="uploading"
            type="file"
            accept="image/*"
            v-on:change="handleFileUpload"
            ref="file"
          >
          <br>
        </label>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6" class="text-right">{{str_example2}}</b-col>
      <b-col cols="6" class="text-left">{{str_upload2}}</b-col>
    </b-row>
    <b-row>
      <b-col cols="6" class="text-right">
        <label class="imgupload">
          <img src="../assets/2.jpg" alt="Face..." class="img-thumbnail imgupload">
        </label>
      </b-col>
      <b-col cols="6" class="text-left">
        <label class="imgupload">
          <img :src="img2" alt="Face..." class="img-thumbnail imgupload cursor">
          <input
            :disabled="uploading"
            type="file"
            accept="image/*"
            v-on:change="handleFileUpload2"
            ref="file2"
          >
          <br>
        </label>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6" class="text-right">{{str_example3}}</b-col>
      <b-col cols="6" class="text-left">{{str_upload3}}</b-col>
    </b-row>
    <b-row>
      <b-col cols="6" class="text-right">
        <label class="imgupload">
          <img src="../assets/3.jpg" alt="Face..." class="img-thumbnail imgupload">
        </label>
      </b-col>
      <b-col cols="6" class="text-left">
        <label class="imgupload">
          <img :src="img3" alt="Face..." class="img-thumbnail imgupload cursor">
          <input
            :disabled="uploading"
            type="file"
            accept="image/*"
            v-on:change="handleFileUpload3"
            ref="file3"
          >
          <br>
        </label>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-right">
        <b-button
          variant="primary"
          :disabled="disOnlyFace"
          @click="compareOnlyFace"
        >{{str_onlyface}}</b-button>
      </b-col>
      <b-col class="text-left">
        <b-button variant="primary" :disabled="disVerify" @click="clickVerify">{{str_verify}}</b-button>
      </b-col>
    </b-row>
    <br>
    <br>
    <b-row>
      <b-col class="text-center">
        <b>{{str_sampledata}}</b>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-right">
        <b-button :disabled="uploading" variant="warning" @click="test1">{{str_samepeople}}</b-button>
      </b-col>
      <b-col class="text-left">
        <b-button :disabled="uploading" variant="warning" @click="test2">{{str_notsamepeople}}</b-button>
      </b-col>
    </b-row>
    <!-- <b-row>
      <b-col class="text-center" style="white-space: pre;">{{str_loading}}</b-col>
    </b-row>-->
    <b-row>
      <b-col class="text-center">
        <b-progress :max="100" :animated="animate" :variant="variant_progress">
          <b-progress-bar :value="uploadPercentage">
            <strong>{{str_loading}} {{ uploadPercentage.toFixed(2) }}%</strong>
          </b-progress-bar>
        </b-progress>
      </b-col>
    </b-row>
    <b-row v-show="false">
      <b-col class="text-center">
        <div id="status" class="ex2" align="left" style="white-space: pre;" v-html="str_result"></div>
        <br>
      </b-col>
    </b-row>
    <b-form v-focusNextOnEnter v-show="!animate">
      <b-row v-for="type in form" :key="type">
        <b-col cols="6" class="text-right">
          <label :for="`type-${type}`" v-if="!type.readonly">{{ type.name }}:</label>
        </b-col>
        <b-col>
          <b-form-input
            v-show="type.ref != 'address'"
            v-model="type.var"
            :id="`${type.id}`"
            :type="type.kind"
            :ref="type.ref"
            v-if="!type.readonly"
            size="sm"
            style="max-width:190px;"
          ></b-form-input>
          <b-form-textarea
            v-show="type.ref == 'address'"
            v-model="type.var"
            size="sm"
            style="max-width:190px;"
          />
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="6"></b-col>
        <b-col cols="6">
          <b-button variant="primary" @click="save">{{str_save}}</b-button>
        </b-col>
      </b-row>
      <!-- <b-row>
        <b-col cols="6" class="text-right">{{this.form[0].name}}: {{this.form[0].var}}</b-col>
        <b-col cols="6">, {{this.form[10].name}}: {{this.form[10].var}}</b-col>
      </b-row>-->
      <b-row>
        <b-col cols="6" class="text-right">
          <label>{{ this.form[0].name }}:</label>
        </b-col>
        <b-col>
          <b-form-input readonly v-model="this.form[0].var" size="sm" style="max-width:190px;"></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="6" class="text-right">
          <label>{{ this.form[10].name }}:</label>
        </b-col>
        <b-col>
          <b-form-input readonly v-model="this.form[10].var" size="sm" style="max-width:190px;"></b-form-input>
        </b-col>
      </b-row>
    </b-form>
    <br>
    <div class="footer-copyright text-center">
      <b-button href="#/about">
        {{str_sponsor}}
        <img fluid rounded src="../assets/en-kku.png" width="40" height="40">&nbsp;
        <img fluid src="../assets/t2p.png" width="40" height="40">&nbsp;
        <img class="img-thumbnail" src="../assets/nsc.png" width="40" height="40">
      </b-button>
    </div>
    <br>
    <b-modal
      ref="myModalRef"
      centered
      hide-footer
      :title="str_modaltitle"
      :header-bg-variant="headerBgVariant"
      header-text-variant="light"
    >
      <div class="d-block text-center" style="white-space: pre;">
        {{str_modal}}
        <span
          :style="{'color': this.str_modalcolor,'font-weight': 'bold','font-size': 'large'}"
        >{{ str_modal2 }}</span>
        {{str_modal3}}
      </div>
      <b-button v-show="false" variant="primary" block @click="hideModal">{{str_modalbutton}}</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "home",
  data: function() {
    return {
      str_example1: "",
      str_example2: "",
      str_example3: "",
      str_upload1: "",
      str_upload2: "",
      str_upload3: "",
      str_verify: "",
      str_onlyface: "",
      str_sampledata: "",
      str_samepeople: "",
      str_notsamepeople: "",
      str_result: "",
      str_loading: " ",
      str_save: "",
      str_modaltitle: "",
      str_modal: "",
      str_modal2: "",
      str_modal3: "",
      str_modalcolor: "",
      str_modalbutton: "",
      str_sponsor: "",
      headerBgVariant: "",
      file: "",
      file2: "",
      file3: "",
      img1: "http://test.adulgun.com/pv/addperson.png",
      img2: "http://test.adulgun.com/pv/init2.jpg",
      img3: "http://test.adulgun.com/pv/init3.jpg",
      note: "",
      uploadPercentage: 0,
      animate: true,
      disVerify: true,
      disOnlyFace: true,
      uploading: false,
      interval: null,
      variant_progress: "",
      e1: "",
      e2: "",
      e3: "",
      form: [
        {
          var: "",
          id: "similarity",
          ref: "similarity",
          name: "",
          kind: "text",
          readonly: true
        },
        {
          var: "",
          id: "nameeng",
          ref: "nameeng",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "name",
          ref: "name",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "gender",
          ref: "gender",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "idnumber",
          ref: "idnumber",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "back",
          ref: "back",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "hbd",
          ref: "hbd",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "issueeng",
          ref: "issueeng",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "exp",
          ref: "exp",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "address",
          ref: "address",
          name: "",
          kind: "text",
          readonly: false
        },
        {
          var: "",
          id: "time",
          ref: "time",
          name: "",
          kind: "text",
          readonly: true
        }
      ]
    };
  },
  mounted() {
    this.setlanguage();
    this.$store.watch(this.$store.getters.getLG, language => {
      this.setlanguage();
    });
  },
  watch: {
    file: {
      handler(val) {
        this.checkfile();
      }
    },
    file2: {
      handler(val) {
        this.checkfile();
      }
    },
    file3: {
      handler(val) {
        this.checkfile();
      }
    }
  },
  methods: {
    checkfile() {
      if (this.file == "" || this.file2 == "" || this.file3 == "")
        this.disVerify = true;
      else this.disVerify = false;
      if (this.file == "" || this.file2 == "") this.disOnlyFace = true;
      else this.disOnlyFace = false;
    },
    setlanguage() {
      var lg = this.$store.state.language;
      if (lg == "TH") {
        this.str_example1 = "ตัวอย่างใบหน้า+บัตร";
        this.str_example2 = "ตัวอย่างหน้าบัตร";
        this.str_example3 = "ตัวอย่างหลังบัตร";
        this.str_upload1 = "1. อัปโหลดใบหน้า+บัตร";
        this.str_upload2 = "2. อัปโหลดหน้าบัตร";
        this.str_upload3 = "3. อัปโหลดหลังบัตร";
        this.str_verify = "4. ยืนยันตัวตน";
        this.str_onlyface = "เปรียบเทียบเฉพาะใบหน้า";
        this.str_sampledata = "ตัวอย่างข้อมูล";
        this.str_samepeople = "คนเดียวกัน";
        this.str_notsamepeople = "ไม่ใช่คนเดียวกัน";
        this.note =
          "หมายเหตุ: รูปภาพที่อัปโหลดถือว่าเป็นข้อมูลส่วนบุคคล จะไม่มีการเปิดเผยข้อมูล";
        this.str_save = "บันทึก";
        this.str_sponsor = "ผู้สนับสนุน";
        this.form[0].name = "ความเหมือน";
        this.form[1].name = "ชื่ออังกฤษ";
        this.form[2].name = "ชื่อไทย";
        this.form[3].name = "เพศ";
        this.form[4].name = "รหัสบัตร";
        this.form[5].name = "รหัสหลังบัตร";
        this.form[6].name = "วันเกิด";
        this.form[7].name = "วันออกบัตร";
        this.form[8].name = "วันหมดอายุ";
        this.form[9].name = "ที่อยู่";
        this.form[10].name = "เวลาประมวลผล(วินาที)";
        if (this.form[0].var == "Same") this.form[0].var = "เหมือน";
        if (this.form[0].var == "Not same") this.form[0].var = "ไม่เหมือน";
        if (this.form[0].var == "Non detect")
          this.form[0].var = "ตรวจจับใบหน้าไม่ได้";
        if (this.form[3].var == "Male") this.form[3].var = "ชาย";
        if (this.form[3].var == "Female") this.form[3].var = "หญิง";
        if (this.str_loading == "Finish") this.str_loading = "เสร็จสิ้น";
        if (this.str_loading == "Uploading...")
          this.str_loading = "กำลังอัปโหลดรูปภาพ...";
        if (this.str_loading == "Verifying...")
          this.str_loading = "กำลังยืนยันตัวบุคคล...";
      } else if (lg == "ENG") {
        this.str_example1 = "Example face + ID Card";
        this.str_example2 = "Example front ID Card";
        this.str_example3 = "Example back ID Card";
        this.str_upload1 = "1. Upload new face + ID Card";
        this.str_upload2 = "2. Upload new front ID Card";
        this.str_upload3 = "3. Upload new back ID Card";
        this.str_verify = "4. Verify";
        this.str_onlyface = "Compare only face";
        this.str_sampledata = "Sample data";
        this.str_samepeople = "Same people";
        this.str_notsamepeople = "Not same people";
        this.note =
          "Note: Uploaded pictures are personal information which can't disclose.";
        this.str_save = "Save";
        this.str_sponsor = "Sponsor";
        this.form[0].name = "Similarity";
        this.form[1].name = "Name ENG";
        this.form[2].name = "Name TH";
        this.form[3].name = "Gender";
        this.form[4].name = "ID number";
        this.form[5].name = "ID number back";
        this.form[6].name = "Birth Date";
        this.form[7].name = "Issue";
        this.form[8].name = "EXP";
        this.form[9].name = "Address";
        this.form[10].name = "Time(second)";
        if (this.form[0].var == "เหมือน") this.form[0].var = "Same";
        if (this.form[0].var == "ไม่เหมือน") this.form[0].var = "Not same";
        if (this.form[0].var == "ตรวจจับใบหน้าไม่ได้")
          this.form[0].var = "Non detect";
        if (this.form[3].var == "ชาย") this.form[3].var = "Male";
        if (this.form[3].var == "หญิง") this.form[3].var = "Female";
        if (this.str_loading == "เสร็จสิ้น") this.str_loading = "Finish";
        if (this.str_loading == "กำลังอัปโหลดรูปภาพ...")
          this.str_loading = "Uploading...";
        if (this.str_loading == "กำลังยืนยันตัวบุคคล...")
          this.str_loading = "Verifying...";
      }
    },
    handleFileUpload(e) {
      // if (e.target.value.length == 0) return;
      this.e1 = e;
      this.file = this.$refs.file.files[0];
      this.img1 = URL.createObjectURL(e.target.files[0]);
    },
    handleFileUpload2(e) {
      // if (e.target.value.length == 0) return;
      this.e2 = e;
      this.file2 = this.$refs.file2.files[0];
      this.img2 = URL.createObjectURL(e.target.files[0]);
    },
    handleFileUpload3(e) {
      // if (e.target.value.length == 0) return;
      this.e3 = e;
      this.file3 = this.$refs.file3.files[0];
      this.img3 = URL.createObjectURL(e.target.files[0]);
    },
    refreshImg() {
      try {
        this.file = this.$refs.file.files[0];
        this.img1 = URL.createObjectURL(this.e1.target.files[0]);
      } catch (error) {
        this.file = "";
        this.img1 = "http://test.adulgun.com/pv/addperson.png";
      }
      try {
        this.file2 = this.$refs.file2.files[0];
        this.img2 = URL.createObjectURL(this.e2.target.files[0]);
      } catch (error) {
        this.file2 = "";
        this.img2 = "http://test.adulgun.com/pv/init2.jpg";
      }
      try {
        this.file3 = this.$refs.file3.files[0];
        this.img3 = URL.createObjectURL(this.e3.target.files[0]);
      } catch (error) {
        this.file3 = "";
        this.img3 = "http://test.adulgun.com/pv/init3.jpg";
      }
    },
    clickVerify() {
      this.refreshImg();
      if (this.file == "" || this.file2 == "" || this.file3 == "") {
        var str = "กรุณาอัปโหลดทั้ง 3 ภาพ";
        if (this.$store.state.language == "ENG") str = "Please upload 3 images";
        this.$parent.showAlert(str, "danger");
        return;
      }
      this.loadImg();
      const formData = new FormData();
      formData.append("face", this.file);
      formData.append("front", this.file2);
      formData.append("back", this.file3);
      axios
        .post(this.$store.state.api + "upload", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: function(progressEvent) {
            this.showProgress(progressEvent);
          }.bind(this)
        })
        .then(response => {
          this.showResult(response.data);
        })
        .catch(e => {});
    },
    showProgress(progressEvent) {
      this.variant_progress = "warning";
      this.uploadPercentage = (progressEvent.loaded * 50) / progressEvent.total;
      if (this.uploadPercentage == 50) this.showVerifying();
    },
    showVerifying() {
      this.variant_progress = "success";
      this.uploadPercentage = 50 + Math.random() * 1;
      var lg = this.$store.state.language;
      if (lg == "TH") {
        this.str_loading = "กำลังยืนยันตัวบุคคล...";
      } else if (lg == "ENG") {
        this.str_loading = "Verifying...";
      }
      this.interval = setInterval(this.incrementTime, 400);
    },
    incrementTime() {
      var rnd = Math.random() * 1;
      rnd = rnd <= 0 ? 0 : rnd;
      this.uploadPercentage += rnd;
      if (this.uploadPercentage >= 94) {
        this.stoptime();
      }
    },
    stoptime() {
      clearInterval(this.interval);
    },
    compareOnlyFace() {
      this.refreshImg();
      if (this.file == "" || this.file2 == "") {
        var str = "กรุณาอัปโหลดทั้ง 2 ภาพ (ใบหน้า และ หน้าบัตร)";
        if (this.$store.state.language == "ENG")
          str = "Please upload 2 images (Face and Front id card)";
        this.$parent.showAlert(str, "danger");
        return;
      }
      this.loadImg();
      const formData = new FormData();
      formData.append("face", this.file);
      formData.append("front", this.file2);
      axios
        .post(this.$store.state.api + "onlyface", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: function(progressEvent) {
            this.showProgress(progressEvent);
          }.bind(this)
        })
        .then(response => {
          this.showResult(response.data);
        })
        .catch(e => {});
    },
    test1() {
      this.img1 = "http://test.adulgun.com/pv/1.jpg";
      this.img2 = "http://test.adulgun.com/pv/2.jpg";
      this.img3 = "http://test.adulgun.com/pv/3.jpg";
      this.loadImg();
      this.showVerifying();
      axios
        .post(this.$store.state.api + "test1")
        .then(response => {
          this.showResult(response.data);
        })
        .catch(e => {});
    },
    test2() {
      this.img1 = "http://test.adulgun.com/pv/4.jpg";
      this.img2 = "http://test.adulgun.com/pv/2.jpg";
      this.img3 = "http://test.adulgun.com/pv/3.jpg";
      this.loadImg();
      this.showVerifying();
      axios
        .post(this.$store.state.api + "test2")
        .then(response => {
          this.showResult(response.data);
        })
        .catch(e => {});
    },
    saveold() {
      axios
        .post(this.$store.state.api + "saveold", this.form, {
          headers: {
            "Content-type": "application/json"
          }
        })
        .then(response => {})
        .catch(e => {});
    },
    save() {
      axios
        .post(this.$store.state.api + "saveresult", this.form, {
          headers: {
            "Content-type": "application/json"
          }
        })
        .then(response => {
          this.animate = true;
        })
        .catch(e => {});
    },
    loadImg() {
      this.uploadPercentage = 0;
      this.uploading = true;
      this.disVerify = true;
      this.disOnlyFace = true;
      this.animate = true;
      var str = "";
      var lg = this.$store.state.language;
      if (lg == "TH") {
        str = "กำลังยืนยันตัวตน...";
        this.str_loading = "กำลังอัปโหลดรูปภาพ...";
      } else if (lg == "ENG") {
        str = "Verifying...";
        this.str_loading = "Uploading...";
      }
      this.str_result = str;
      this.toBottom();
    },
    showResult(d) {
      this.stoptime();
      this.uploading = false;
      this.checkfile();
      this.uploadPercentage = 100;
      this.animate = false;
      var str = "";
      var lg = this.$store.state.language;
      this.form[1].var = d.firstnameeng + " " + d.lastnameeng;
      this.form[2].var = d.name;
      this.form[3].var = d.gender;
      this.form[4].var = d.idnumber;
      this.form[5].var = d.back;
      this.form[6].var = d.hbd;
      this.form[7].var = d.issueeng;
      this.form[8].var = d.exp;
      this.form[9].var = d.address
        .replace("ที่อยู่ ", "")
        .replace("\n", " ")
        .trim();
      this.form[10].var = d.time;
      this.form[10].id = d.runnumber;
      if (lg == "TH") {
        this.str_modalbutton = "ตกลง";
        this.str_modaltitle = "ผลลัพธ์การยืนยันตัวบุคคล";
        str += "ความเหมือน: ";
        if (d.similarity == "same") {
          str += "<font color='green'>เหมือน</font>";
          this.form[0].var = "เหมือน";
          this.str_modal = "รูปใบหน้าของเจ้าของบัตรกับ\nรูปใบหน้าบนบัตรประชาชน";
          this.str_modal2 = "พบว่ามีความคล้ายคลึงกัน";
          this.str_modal3 = "การยืนยันตัวบุคคลเสร็จสมบูรณ์";
          this.str_modalcolor = "green";
          this.headerBgVariant = "success";
        } else if (d.similarity == "not same") {
          str += "<font color='red'>ไม่เหมือน</font>";
          this.form[0].var = "ไม่เหมือน";
          this.str_modal = "รูปใบหน้าของเจ้าของบัตรกับ\nรูปใบหน้าบนบัตรประชาชน";
          this.str_modal2 = "พบว่าไม่มีความคล้ายคลึงกัน";
          this.str_modal3 = "";
          this.str_modalcolor = "red";
          this.headerBgVariant = "danger";
        } else if (d.similarity == "non detect") {
          str += "<font color='red'>ตรวจจับใบหน้าไม่ได้</font>";
          this.form[0].var = "ตรวจจับใบหน้าไม่ได้";
          this.str_modal = "";
          this.str_modal2 = "ไม่สามารถตรวจจับ";
          this.str_modal3 =
            "รูปใบหน้าของเจ้าของบัตรหรือ\nรูปใบหน้าบนบัตรประชาชน";
          this.str_modalcolor = "red";
          this.headerBgVariant = "danger";
        }
        if (d.firstnameeng)
          str += "\nชื่ออังกฤษ: " + d.firstnameeng + " " + d.lastnameeng;
        if (d.name) str += "\nชื่อไทย: " + d.name;
        if (d.gender) {
          str += "\nเพศ: ";
          if (d.gender == "Male") {
            str += "ชาย";
            this.form[3].var = "ชาย";
          } else if (d.gender == "Female") {
            str += "หญิง";
            this.form[3].var = "หญิง";
          }
        }
        if (d.idnumber) str += "\nรหัสบัตร: " + d.idnumber;
        if (d.back) str += "\nรหัสหลังบัตร: " + d.back;
        if (d.hbd) str += "\nวันเกิด: " + d.hbd;
        if (d.issueeng) str += "\nวันออกบัตร: " + d.issueeng;
        if (d.exp) str += "\nวันหมดอายุ: " + d.exp;
        this.str_loading = "เสร็จสิ้น";
        this.saveold();
      } else if (lg == "ENG") {
        this.str_modalbutton = "OK";
        this.str_modaltitle = "Result";
        str += "Similarity: ";
        if (d.similarity == "same") {
          str += "<font color='green'>Same</font>";
          this.form[0].var = "Same";
          this.str_modal = "The face and face-id-card";
          this.str_modal2 = "are same.";
          this.str_modal3 = "";
          this.str_modalcolor = "green";
          this.headerBgVariant = "success";
        } else if (d.similarity == "not same") {
          str += "<font color='red'>Not same</font>";
          this.form[0].var = "Not same";
          this.str_modal = "The face and face-id-card";
          this.str_modal2 = "are different.";
          this.str_modal3 = "";
          this.str_modalcolor = "red";
          this.headerBgVariant = "danger";
        } else if (d.similarity == "non detect") {
          str += "<font color='red'>Non detect</font>";
          this.form[0].var = "Non detect";
          this.str_modal = "The face and face-id-card";
          this.str_modal2 = "can not detect.";
          this.str_modal3 = "";
          this.str_modalcolor = "red";
          this.headerBgVariant = "danger";
        }
        if (d.firstnameeng)
          str += "\nName ENG: " + d.firstnameeng + " " + d.lastnameeng;
        if (d.name) str += "\nName TH: " + d.name;
        if (d.gender) str += "\nGender: " + d.gender;
        if (d.idnumber) str += "\nID number: " + d.idnumber;
        if (d.back) str += "\nID number back: " + d.back;
        if (d.hbd) str += "\nBirth Date: " + d.hbd;
        if (d.issueeng) str += "\nIssue: " + d.issueeng;
        if (d.exp) str += "\nEXP: " + d.exp;
        this.str_loading = "Finish";
      }
      this.str_result = str;
      this.toBottom();
      this.showModal();
    },
    toBottom() {
      var that = this;
      setTimeout(function() {
        var scrollingElement = document.scrollingElement || document.body;
        scrollingElement.scrollTop = scrollingElement.scrollHeight;
      }, 10);
    },
    showModal() {
      this.$refs.myModalRef.show();
    },
    hideModal() {
      this.$refs.myModalRef.hide();
    }
  }
};
</script>

